body {
  margin: 0 auto;
  font-family: "Montserrat";
  padding-top: 40px;
}

.red {
  color: #ff6060;
}

@media screen and (max-width: 900px) {
  .kasa {
    width: 80%;
  }
}
@media screen and (max-width: 450px) {
  .kasa {
    width: 100px;
  }
}
@media screen and (max-width: 370px) {
  .kasa {
    width: 80px;
  }
}

.header {
  align-items: center;
  display: flex;
  flex-direction: row;
  height: 68px;
  justify-content: space-between;
  max-width: 80%;
  margin: 0 auto;
  padding-bottom: 40px;
}
@media screen and (max-width: 900px) {
  .header {
    padding-bottom: 0px;
  }
}
.header_list {
  color: #ff6060;
  display: flex;
  font-size: clamp(12px, 2.5vw, 24px);
  gap: 50px;
  text-decoration: none;
  list-style: none;
}
.header_links {
  display: flex;
  text-decoration: none;
  color: #ff6060;
  cursor: pointer;
}
@media screen and (max-width: 900px) {
  .header_links {
    font-size: clamp(20px, 3vw, 48px);
  }
}

.active {
  text-decoration: underline;
}

.banner {
  display: flex;
  justify-content: center;
  align-items: center;
  color: #f6f6f6;
  position: relative;
  border-radius: 25px;
  background-image: linear-gradient(rgba(0, 0, 0, 0.3), rgba(0, 0, 0, 0.3)), url("../images/IMGpng.png");
  background-position: center;
  background-size: cover;
  height: 223px;
  max-width: 83%;
  margin: 0 auto;
}
@media screen and (max-width: 900px) {
  .banner {
    margin: 20px auto;
    justify-content: flex-start;
  }
}
@media screen and (max-width: 500px) {
  .banner {
    margin: 20px auto;
    height: 120px;
    justify-content: flex-start;
  }
}
.banner_about {
  position: relative;
  border-radius: 25px;
  height: 223px;
  max-width: 82%;
  margin: 0 auto;
  background-image: linear-gradient(rgba(0, 0, 0, 0.3), rgba(0, 0, 0, 0.3)), url("../images/IMG2.png");
  background-position: center;
  background-size: cover;
}
@media screen and (max-width: 550px) {
  .banner_about {
    margin: 20px auto;
    height: 120px;
  }
}
.banner p {
  font-size: clamp(20px, 4vw, 48px);
}
@media screen and (max-width: 900px) {
  .banner p {
    margin-left: 5%;
    width: 55%;
    font-size: clamp(20px, 5vw, 48px);
  }
}

.grid {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #f6f6f6;
  margin: 0 auto;
  width: 83%;
  height: auto;
  border-radius: 20px;
  margin-top: 50px;
}
@media screen and (max-width: 900px) {
  .grid {
    background-color: white;
    width: 100%;
  }
}
.grid_appartements {
  display: grid;
  grid-template-rows: repeat(2, 1fr);
  grid-template-columns: repeat(3, 1fr);
  width: 100%;
  height: 80%;
  gap: 50px;
  margin: 50px;
}
@media screen and (max-width: 900px) {
  .grid_appartements {
    display: flex;
    flex-direction: column;
    width: 83%;
    margin: 0;
  }
}

.square {
  position: relative;
  width: 100%;
  height: 340px;
  border-radius: 10px;
  cursor: pointer;
  overflow: hidden;
  transition: box-shadow 0.2s ease-in-out;
}
@media screen and (max-width: 900px) {
  .square {
    width: 100%;
  }
}
@media screen and (max-width: 500px) {
  .square {
    height: 200px;
  }
}
.square::after {
  content: "";
  z-index: 10;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(180deg, rgba(255, 255, 255, 0) 0%, rgba(0, 0, 0, 0.5) 100%);
}
.square img {
  width: 100%;
  height: 100%;
  -o-object-fit: cover;
     object-fit: cover;
  -o-object-position: center;
     object-position: center;
  transition: transform 0.2s ease-in-out;
  will-change: transform;
}
.square h3 {
  width: 50%;
  z-index: 20;
  position: absolute;
  bottom: 20px;
  left: 20px;
  right: 20px;
  font-weight: 500;
  font-size: clamp(12px, 3vw, 18px);
  color: white;
}

.footer {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 240px;
  background-color: black;
  margin-top: 50px;
  flex-direction: column;
}

.white {
  color: white;
  font-size: 20px;
  margin-top: 30px;
}

.error {
  color: #ff6060;
  display: flex;
  justify-content: center;
  font-size: 200px;
  position: relative;
  align-items: center;
  bottom: 100px;
}
.error_text {
  display: flex;
  justify-content: center;
  color: #ff6060;
  align-items: center;
  flex-direction: column;
  position: relative;
  bottom: 80px;
}
.error_link {
  color: #ff6060;
}

.decoration {
  text-decoration: none;
}

.carousel {
  position: relative;
  display: flex;
  width: 100%;
  height: 415px;
  border-radius: 25px;
  background-position: 50%;
  background-size: cover;
  width: 83%;
  margin: 0 auto;
}
@media screen and (max-width: 650px) {
  .carousel {
    height: 200px;
    margin: 20px auto;
  }
}
.carousel_arrow {
  position: absolute;
  top: 50%;
  height: 15%;
  transform: translateY(-50%);
  cursor: pointer;
}
.carousel_arrow:active {
  transform: translateY(-50%) scale(0.9);
}
.carousel_arrow_right {
  right: 25px;
}
.carousel_arrow_left {
  left: 25px;
}
.carousel .slideCount {
  position: absolute;
  color: #f6f6f6;
  bottom: 25px;
  left: 50%;
  transform: translateX(-50%);
  font-size: clamp(14px, 2vw, 18px);
  font-weight: 500;
}

.logement_infos {
  align-items: flex-start;
  color: #ff6060;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  margin: 0 auto;
  width: 83%;
}
@media screen and (max-width: 650px) {
  .logement_infos {
    flex-direction: column;
  }
}
.logement_infos_tags_container {
  display: flex;
  align-items: center;
  gap: 10px;
}
.logement_infos_tags {
  background-color: #ff6060;
  display: flex;
  align-items: center;
  justify-content: center;
  color: #f6f6f6;
  width: auto;
  height: 25px;
  border-radius: 10px;
  padding-left: 5px;
  padding-right: 5px;
}
.logement_infos_stars {
  display: flex;
  position: relative;
  gap: 10px;
  top: 40px;
}
.logement_infos_stars img {
  width: 30px;
  height: 30px;
}
@media screen and (max-width: 650px) {
  .logement_infos_stars img {
    width: 20px;
    height: 20px;
  }
}

.name {
  color: #ff6060;
  align-items: flex-end;
  display: flex;
  gap: 10px;
  justify-content: flex-end;
  position: relative;
  top: 20px;
}
.name_column {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  position: relative;
  bottom: 10px;
}

.host {
  display: flex;
  flex-direction: column;
  gap: 5px;
}
@media screen and (max-width: 650px) {
  .host {
    flex-direction: row-reverse;
    justify-content: space-between;
    width: 100%;
  }
}
.host_picture {
  border-radius: 90px;
  width: 64px;
  height: 64px;
}

.collapse {
  max-width: 100%;
  border-radius: 10px;
  margin: 0 auto;
  color: #f6f6f6;
  background-color: #ff6060;
}
.collapse_title {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  border-radius: 10px;
  font-weight: 500;
  font-size: clamp(13px, 2vw, 18px);
  padding: 10px 10px;
  cursor: pointer;
}
.collapse_title .arrow {
  transition: all 0.2s ease-in-out;
}
@media screen and (max-width: 1230px) {
  .collapse_title .arrow {
    width: 12px;
  }
}
.collapse_title .arrow_up {
  transform: rotate(180deg);
}
.collapse_title .arrow_down {
  transform: rotate(0deg);
}
.collapse_content {
  color: white;
  background-color: #ff6060;
  border-radius: 5px;
  display: flex;
  flex-direction: column;
  gap: 5px;
  letter-spacing: 1px;
  visibility: visible;
  opacity: 1;
  height: auto;
  padding: 10px;
  letter-spacing: 1px;
  font-size: clamp(12px, 2vw, 16px);
  transition: visibility 0.5s ease-in-out, opacity 0.5s ease-in-out, height 0.2s ease-in-out;
}
.collapse_content_hidden {
  visibility: hidden;
  opacity: 0;
  height: 0;
}

.accomodation_collapse {
  display: flex;
  justify-content: space-between;
  gap: 70px;
  margin: 30px auto;
  width: 83%;
}
.accomodation_collapse_item {
  width: 50%;
}
@media screen and (max-width: 650px) {
  .accomodation_collapse {
    flex-direction: column;
    gap: 20px;
    margin: 20px auto;
  }
  .accomodation_collapse_item {
    width: 100%;
  }
}

.accomodation_collapse2 {
  display: flex;
  justify-content: space-between;
  flex-direction: column;
  gap: 10px;
  margin: 30px auto;
  width: 95%;
}
.accomodation_collapse2_item {
  width: 50%;
}
@media screen and (max-width: 650px) {
  .accomodation_collapse2 {
    flex-direction: column;
    gap: 20px;
    margin: 20px auto;
  }
  .accomodation_collapse2_item {
    width: 100%;
  }
}

.oups {
  font-size: 30px;
  position: relative;
  bottom: 100px;
}/*# sourceMappingURL=index.css.map */